.Frame {
  display: grid;

  grid-template-columns: auto auto auto;

  row-gap: 3.75rem;
  column-gap: 6.5rem;
  grid-auto-flow: row;

  position: relative;
  width: 100%;
  height: auto;

  margin-top: 4.63rem;
  bottom: 1.25rem;
}
.Error {
  width: 25rem;
  height: 6.25rem;
  margin-left: 25rem;
  font-size: 2rem;
  color: #02014f;

  text-align: center;
}

@media (min-width: 578px) and (max-width: 767px) and (orientation: landscape) {
  .Frame {
    grid-template-columns: auto auto;
    column-gap: 2.5rem;
  }
  .Error {
    margin: 0 auto;
  }
}
@media (min-width: 578px) and (max-width: 767px) and (orientation: portrait) {
  .Frame {
    grid-template-columns: auto auto;
    column-gap: 2.5rem;
  }
  .Error {
    margin: 0 auto;
  }
}

/* Small devices 576px */
@media (max-width: 35.938rem) {
  .Frame {
    grid-auto-flow: row;
    justify-content: center;
    grid-template-columns: auto;
  }
  .Error {
    margin: 0 auto;
  }
}
/* Medium devices 768px */
@media only screen and (min-width: 48rem) {
  .Frame {
    grid-template-columns: auto auto;
    justify-content: center;
    column-gap: 4.5rem;
  }
  .Error {
    margin: 0 auto;
  }
}
/* Large devices  992px */
@media only screen and (min-width: 62rem) {
  .Frame {
    grid-template-columns: auto auto auto;
    justify-content: center;
    column-gap: 4.5rem;
  }
  .Error {
    margin: 0 auto;
  }
}
/* X-Large devices 1200px */
@media only screen and (min-width: 75rem) {
  .Frame {
    place-content: center;
  }
  .Error {
    margin: 0 auto;
  }
}
/* XX-Large devices  1400px*/
@media only screen and (min-width: 87.5rem) {
  .Frame {
    column-gap: 12.5rem;
    justify-content: center;
  }
  .Error {
    margin: 0 auto;
  }
}
